 body{
  font-family: 'Nunito Sans', sans-serif;
}

.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: auto;
    width: auto;
    padding: 0px;
    opacity: 0;
    transition: .5s ease;
    background-color: #006ddf;
    border-radius: 25px;
  }

  .text {
      color: white;
      font-size: 20px;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      text-align: center;
  }
.container {
position: relative;
text-align: center;
color: white;
width: 100%;
}

/* Bottom left text */
.bottom-left {
position: absolute;
bottom: 8px;
left: 16px;
}


.bottom-left {
position: absolute;
bottom: 2%;
left: 2%;
font-size: 2vw;
padding: 0;
}

a:link {
color: white;
text-decoration: none;
}
/* A link that has been visited */
a:visited {
color: white;
text-decoration: none;
}

.grid-container {
  display: grid;
}

.grid-container--fit {
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
}


.grid-element {
padding: 20px;
position: relative;

}
.grid-element-links {
  padding: 20px;
border-radius: 25px;
position: relative;


}
.useful-links{
  color: #7413dc;
  margin: none;
}
useful-links{
  padding: 0;
  color: #7413dc;
  margin: none;
}
.more-info{
  position: relative;
  color:#006ddf;
  padding: 0;
  margin: 0;
}
.follow-us{
  padding: 20px;
  border-radius: 25px;
  color:#25b755;
  padding: 0;
