}
.grid-element-welcome {
position: relative;

}
.grid-element:hover .overlay {
  opacity: 1;
}

body {
padding: 1em;
}


.button{
position: absolute;
bottom: 1%;
left: 50%;
transform: translate(-50%, -50%);
font-size: 2vw;
width: auto;
background-color: #006ddf;
color: #ffffff;
padding: 2% 4%;
text-align: center;
border: none;

}
.button:hover{
  background-color: #ffffff;
color: #006ddf;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}


.navbar {
  overflow: hidden;
  background-color: #006ddf;
  position: relative;
  bottom: 0;
  width: 100%;
}

.navbar button {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
  background-color: #006ddf;
  border: none;

}

.navbar button:hover {
  background-color: #ddd;
  color: black;
}

.navbar button.active {
  background-color: #4CAF50;
  color: white;
}

.navbar .icon {
  display: none;
}

@media screen and (max-width: 600px) {
  .navbar a:not(:first-child) {
      display: none;
  }

  .navbar a.icon {
      float: right;
      display: block;
  }
}

@media screen and (max-width: 600px) {
  .navbar.responsive .icon {
      right: 0;
      bottom: 0;
  }

  .navbar.responsive a {
      float: none;
      display: block;
      text-align: left;
  }

}